import React from 'react';
import {
    Box,
    Grid,
    IconButton,
    TextField
} from '@material-ui/core';
import {
    AddAPhoto as AddAPhotoIcon,
    Delete as DeleteIcon
} from '@material-ui/icons'
import {useDropzone} from 'react-dropzone';
import {
    makeStyles
} from '@material-ui/styles';
import R from 'ramda';
import Required from '@/components/required';

const useStyles = makeStyles({
    flexbox: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center'
    },
    img: {
        display: 'block',
        width: '100%',
        height: '90px'
    },
    deleteIcon: {
        color: 'red'
    }
});
interface IProps {
    labelText: string;
    index?: number;
    photo: any[];
    note:string;
    textAreaRequired: boolean;
    saveContent?: (params: object) => void;
    saveReviewDetail?: (params: object) => void;
}
const TextAreaAndUploadImg: React.FC<IProps> = (props: any) => {
    const classes = useStyles();
    const {getRootProps, getInputProps} = useDropzone({
        accept: 'image/*',
        onDrop: acceptedFiles => {
            const photoFormat: any = props.photo.concat(acceptedFiles.map(file => Object.assign(file, {
                preview: URL.createObjectURL(file)
            })));
            // props.saveContent({
            //     photo: photoFormat,
            //     index: props.index
            // });
            props.saveReviewDetail({
                photo: photoFormat
            });
        }
    });
    const deleteImg = (i: number) => {
        const photoClone = R.remove(i, 1, props.photo);
        props.photo.forEach((file: any) => URL.revokeObjectURL(file.preview));
        // props.saveContent({
        //     photo: photoClone,
        //     index: props.index
        // });
        props.saveReviewDetail({
            photo: photoClone
        });
    }

    return (
        <Grid container>
            <Grid item xs>
                <Grid container alignItems="center">
                    <If condition={props.textAreaRequired}>
                        <Grid item xs={1}>
                            <Required />
                        </Grid>
                    </If>
                    <Grid item xs>
                        <TextField
                            id="outlined-textarea"
                            label={props.labelText}
                            multiline
                            margin="normal"
                            variant="outlined"
                            fullWidth
                            value={props.note}
                            // onChange={e => props.saveContent({
                            //     note: e.target.value,
                            //     index: props.index
                            // })}
                            onChange={e => props.saveReviewDetail({
                                note: e.target.value
                            })}
                        />
                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={2} className={classes.flexbox}>
                     <Box {...getRootProps({className: 'dropzone'})}>
                        <input {...getInputProps()} />
                        <IconButton>
                            <Required />
                            <AddAPhotoIcon color="primary" />
                        </IconButton>
                    </Box>
            </Grid>
            <Grid container spacing={1} alignItems="center">
                {
                    props.photo.map((item: any, i: any) => (
                        <Grid item xs={4} key={`img-file-${i}`}>
                            <Box position="relative">
                                <img
                                    className={classes.img}
                                    src={item.preview}
                                    alt={`img-file-${i}`}
                                />
                                <Box position="absolute" top="-20px" right="-20px">
                                    <IconButton
                                        className={classes.deleteIcon}
                                        onClick={() => deleteImg(i)}
                                    >
                                        <DeleteIcon />
                                    </IconButton>
                                </Box>
                            </Box>
                        </Grid>
                    ))
                }
            </Grid>
        </Grid>
    )
}
export default TextAreaAndUploadImg;