import React from 'react';
import {
    Grid,
    CircularProgress,
    IconButton,
    Paper,
    InputBase
} from '@material-ui/core';
import {
    KeyboardBackspace,
    Search,
    Done as DoneIcon,
} from '@material-ui/icons';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import useStyles from './styles';

interface IProps extends RouteComponentProps {
    title: string;
    endAdorment?: JSX.Element;
    useSearch?: boolean;
    searchPlaceholder?: string;
    searchLoading?: boolean;
    getSearchValue?: (val: string) => void;
    beforeRouteLeave?: () => void;
}

const  AppBar: React.FC<IProps> = props => {
    const classes = useStyles();
    const [searchState, setSearchState] = React.useState(false);
    const [searchValue, setSearchValue] = React.useState('');

    const handleClickBack = () => {
        props.beforeRouteLeave && props.beforeRouteLeave();
        props.history.goBack();
    };
    const handleSetSearchState = (state: boolean) => {
        setSearchState(state);
        props.getSearchValue && props.getSearchValue(searchValue);
        // setSearchValue('');
    };
    
    return (
        <Paper className={classes.root} square={true}>
            <Grid container alignItems="center">
                <Grid item xs={2}>
                    <IconButton onClick={() => handleClickBack()}>
                        <KeyboardBackspace className={classes.icon} />
                    </IconButton>
                </Grid>
                <Choose>
                    <When condition={!props.useSearch}>
                        <Grid item xs>
                            {props.title}
                        </Grid>
                        <Grid item xs={2}>
                            {props.endAdorment}
                        </Grid>
                    </When>
                    <Otherwise>
                        <Grid item xs="auto" className={classes.inputBox}>
                            <InputBase
                                autoFocus
                                value={searchValue}
                                onChange={e => setSearchValue(e.target.value)}
                                className={classes.input}
                                placeholder={props.searchPlaceholder}
                                inputProps={{ 'aria-label': props.searchPlaceholder }}
                            />
                            <IconButton
                                className={classes.iconButton}
                                aria-label="search"
                                onClick={() => handleSetSearchState(false)}
                            >
                                <Choose>
                                    <When condition={props.searchLoading}>
                                        <CircularProgress size={20} color="secondary" />
                                    </When>
                                    <Otherwise>
                                        <Search className={classes.icon} />
                                    </Otherwise>
                                </Choose>
                                
                            </IconButton>
                        </Grid>
                    </Otherwise>
                </Choose>
            </Grid>
        </Paper>
    )
}

export default withRouter(AppBar);