import React,{ useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { mergeMap } from 'rxjs/operators';
import { makeStyles } from '@material-ui/styles';
import {
    Grid,
    Switch,
    TextField,
    Button,
    ButtonGroup,
    Radio,
    RadioGroup,
    FormControl,
    FormControlLabel
} from '@material-ui/core';
import Search from '@/components/search/index';
import { fetchCanUseFarms$ } from '@/api';
import "./index.scss";

const useStyles = makeStyles({
    container: {
        width: '70%',
        margin: '15vh auto 0'
    },
    title: {
        fontSize: '20px'
    },
    textField: {
        marginTop: '40px'
    },
    ranchList: {
        paddingLeft: 10,
        marginTop: 10
    }
});

interface aldRanchItem {
    farm_code: string;
    farm_name: string;
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        saveUserInfo: (userInfo: object) => dispatch({
            type: 'SAVE_USER_INFO',
            payload: {
                userInfo
            }
        })
    }
};
const SelectRanch = (props: any) => {
    const classes = useStyles();
    const [otherRanchState, setOtherRanchState] = React.useState(false);
    const [checkALD, setCheckALD] = useState(true);
    const [aldRanch, setAldRanch] = useState('');
    const [searchValue, setSearchValue] = useState('');
    const [aldRanchList, setAldRanchList] = useState<aldRanchItem[]>([]);

    useEffect(() =>  {
        const Params: any = {
            page: 1,
            page_size: 1000
        };
        if (searchValue) {
            Params.search = searchValue
        }
        const subscrible =  fetchCanUseFarms$(Params).pipe(
            mergeMap(({ response = {} }) => {
                return Promise.resolve(response.results || []);
            })
        ).subscribe(
            data => {
                console.log('data', data);
                setAldRanchList(data);
                setAldRanch((data[0] && data[0].farm_code) || '');
            },
            error => {
                console.log('error', error);
            }
        )
        return () => {
            subscrible.unsubscribe();
        }
    }, [searchValue]);

    const handleChange = (e: any) => {
        setCheckALD(e.target.checked)
    };

    const handleALDRanchChange = (e: any) => {
        setAldRanch(e.target.value)
    }

    const [other, setOtherRanch] = useState("");
    const handleInputOtherRanch = (e: any) => {
        setOtherRanch(e.target.value);
    }

    const jumpToHome = () => {
        let isALD = "0";
        if (checkALD) {
            isALD = "1";
            props.saveUserInfo({farm_code: aldRanch});
        } else {
            setOtherRanchState(!other);
            if (!other) return;
        }
        props.history.push(`/home?isALD=${isALD}`);
        sessionStorage.setItem("isALD", isALD);
    };

    return (
        <div className="select-ranch-wrapper">
            <div className="select-ranch-container">
                <Grid container alignItems="center" justify="space-between">
                    <Grid item xs={8}>
                        <h1 className={classes.title}>登陆阿拉钉牧场</h1>
                    </Grid>
                    <Grid item xs={4} className="text-right">
                        <Switch
                            checked={checkALD}
                            color="primary"
                            onChange={handleChange}
                        />
                    </Grid>
                </Grid>
                <Choose>
                    <When condition={checkALD}>
                        <Search
                            searchMethod={setSearchValue}
                        />
                        <FormControl component="fieldset" className={classes.ranchList}>
                            <RadioGroup aria-label="position" name="position" value={aldRanch} onChange={handleALDRanchChange}>
                                {
                                    aldRanchList.map(item => (
                                        <FormControlLabel
                                            key={item.farm_code}
                                            value={item.farm_code}
                                            control={<Radio color="primary" />}
                                            label={item.farm_name}
                                            labelPlacement="end"
                                        />
                                    ))
                                }
                            </RadioGroup>
                        </FormControl>
                    </When>
                    <Otherwise>
                        <TextField
                            error={otherRanchState}
                            className={classes.textField}
                            placeholder="录入您的牧场名称,如XXX牧场"
                            fullWidth
                            value={other}
                            onChange={handleInputOtherRanch}
                            helperText={otherRanchState ? "请录入您的牧场名称" : ""}
                        />
                    </Otherwise>
                </Choose>
                <ButtonGroup
                    className={classes.textField}
                    fullWidth
                    size="medium"
                    onClick={jumpToHome}
                    variant="contained"
                    color="primary"
                    aria-label="Full width outlined button group"
                >
                    <Button>
                        确定
                    </Button>
                </ButtonGroup>
            </div>
        </div>
    )
}

export default connect(null, mapDispatchToProps)(SelectRanch)