import React from 'react';
import { Button as MButton, CircularProgress, ButtonGroup } from '@material-ui/core';

interface IProps {
    loading: boolean;
    btnText: string;
    onClick?: () => void;
    className?: string | undefined;
}

const Button: React.FC<IProps> = props => {

    return (
        <ButtonGroup
            onClick={() => props.onClick && props.onClick()}
            className={props.className}
            fullWidth
            size="medium"
            variant="contained"
            color="primary"
            aria-label="Full width outlined button group"
        >
            <MButton>
                <Choose>
                    <When condition={props.loading}>
                        <CircularProgress size={18} color="secondary" style={{
                            marginRight: '10px'
                        }} />正在操作中...
                    </When>
                    <Otherwise>
                        {props.btnText}
                    </Otherwise>
                </Choose>
            </MButton>
        </ButtonGroup>
    )
}
export default Button;