import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
    BottomNavigation,
    BottomNavigationAction
} from '@material-ui/core';
import {
    Search as SearchIcon,
    Home as HomeIcon,
    Description as DescriptionIcon,
    TrendingUp as TrendingUpIcon
} from '@material-ui/icons';
import { ROUTE_HOME_PATH } from "../../config";

const useStyles = makeStyles({
    layout: {
        height: '100vh'
    },
    bottomNav: {
        position:'fixed',
        width: '100%',
        bottom: 0,
        zIndex: 10,
        boxShadow: "0 -1px 5px 0 rgba(58, 59, 59, 0.5)"
    },
});

interface IProps {
    location: {
        pathname: string;
    },
    history: {
        push: (pathname: string) => void;
    }
}

export default (Component: any) => (props: IProps) => {
    const classes = useStyles();
    const { location, history } = props;
    const value = ROUTE_HOME_PATH.includes(location.pathname) ? ROUTE_HOME_PATH[1] : location.pathname;

    return (
        <div className={classes.layout}>
            <Component {...props} />

            <BottomNavigation
                value={value}
                onChange={(e, pathname) => history.push(pathname)}
                className={classes.bottomNav}
                showLabels
            >
                <BottomNavigationAction label="主页" value={ROUTE_HOME_PATH[1]} icon={<HomeIcon />} />
                <BottomNavigationAction label="任务" value="/tasks" icon={<DescriptionIcon />} />
                <BottomNavigationAction label="报表" value="/reports" icon={<TrendingUpIcon />} />
                <BottomNavigationAction label="查牛" value="/search" icon={<SearchIcon />} />
            </BottomNavigation>
        </div>
    )
};
