import {
    SAVE_ASSESSER,
    SAVE_COW,
    SAVE_COWSHED,
    SAVE_REVIEW_DETAIL,
    SAVE_CONTENT,
    ADD_CONTENT,
    CLEAR_STATE
} from './actionTypes';

interface Info {
    value: string;
    label: string;
}

export const saveEstimator = (info: Info) => ({
    type: SAVE_ASSESSER,
    payload: {
        ...info
    }
});

export const saveCow = (info: Info) => ({
    type: SAVE_COW,
    payload: {
        ...info
    }
});

export const saveCowshed = (info: Info) => ({
    type: SAVE_COWSHED,
    payload: {
        ...info
    }
});

export const saveReviewDetail = (info: Info) => ({
    type: SAVE_REVIEW_DETAIL,
    payload: info
});

export const saveContent = (info: Info) => ({
    type: SAVE_CONTENT,
    payload: info
});

export const addContent = (info: Info) => ({
    type: ADD_CONTENT,
    payload: info
});

export const clearState = (info: Info) => ({
    type: CLEAR_STATE,
    payload: info
});