import React, { useEffect } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import {
    Table,
    TableHead,
    TableBody,
    TableRow,
    TableCell,
    Box,
    Paper,
    CircularProgress
} from '@material-ui/core';
import {
    makeStyles
} from '@material-ui/styles';
import clsx from 'clsx';
import AppBar from '@/components/app-bar';
import { getOnsiteAssessments$ } from '@/api';
import { map } from 'rxjs/operators';

interface SceneReportListItem {
    name: string;
    date: string;
    type: string;
    id: string | number;
}
const createData = (name: string, date: string, type: string, id: string | number) => {
    return { name, date, type, id }
}
const SceneReportList: SceneReportListItem[] = [
    createData('风清扬', '2019-03-10', '乳头评分', 1),
    createData('任我行', '2019-03-10', '行走评分', 2),
    createData('风清扬', '2019-03-10', '巡棚评分', 3),
    createData('任我行', '2019-03-10', '体况评分', 4)
];
const useStyles = makeStyles((theme: any) => ({
    root: {
        paddingTop: 48
    },
    tableHeader: {
        color: theme.palette.primary.main,
    },
    circularProgress: {
        marginRight: 10
    },
    tableItemWidth: {
        width: '22vw'
    },
    tablePadding: {
        padding: '14px 0px'
    }
}))
const SceneReport: React.FC<RouteComponentProps> = props => {
    const classes = useStyles();

    const [params, setParams] = React.useState({
        page: 1,
        page_size: 10
    });
    const [List, setList] = React.useState([]);
    const [next, setNext] = React.useState(null);
    const [loading, setLoading] = React.useState(false);
    const tableHeaderCls = clsx(classes.tableHeader, classes.tablePadding);

    const loadMore = () => {
        if (next) {
            setParams({
                ...params,
                page: params.page + 1
            });
        }
    };

    useEffect(() => {
        setLoading(true);
        const ApiInst = getOnsiteAssessments$(params).pipe(
            map(({response}) => response),
            map(response => [response.results, response.next])
        ).subscribe(
            (data: any) => {
                console.log('data', data);
                const list = data[0];
                setList(List.concat(list));
                setNext(data[1]);
                setLoading(false);
            }
        );
        return () =>  ApiInst.unsubscribe();
    }, [params]);

    return (
        <Box className={classes.root}>
            <AppBar title="现场报告-清单" />
            <Paper square>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell className={tableHeaderCls} align="center">人员</TableCell>
                            <TableCell className={tableHeaderCls} align="center">日期</TableCell>
                            <TableCell className={tableHeaderCls} align="center">类型</TableCell>
                            <TableCell className={tableHeaderCls} align="center">详情</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {List.map((listItem: any) => (
                            <TableRow
                                key={listItem.id}
                                // onClick={() => props.history.push(`/sceneReportDetail/${listItem.id}`)}
                            >
                                <TableCell align="center" className={classes.tablePadding}>{listItem.assessr_name}</TableCell>
                                <TableCell align="center" className={classes.tablePadding}>{listItem.assess_date}</TableCell>
                                <TableCell align="center" className={classes.tablePadding}>{listItem.assesstype_name}</TableCell>
                                <TableCell align="center" className={classes.tablePadding}>
                                    <a href={listItem.pdf} download className={classes.tableHeader}>
                                        下载
                                    </a>
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </Paper>
            <Choose>
                <When condition={loading}>
                    <Box display="flex" justifyContent="center" alignItems="center" className="text-center" marginTop="20px" fontSize="12px">
                        <CircularProgress size={15} className={classes.circularProgress} />正在加载中...
                    </Box>
                </When>
                <Otherwise>
                    <Box className="text-center" marginTop="20px" onClick={() => loadMore()} fontSize="12px">
                        {next ? '点击加载更多' : '没有更多了'}
                    </Box>
                </Otherwise>
            </Choose>
        </Box>
    )
};
export default SceneReport;