const defaultState = () => ({
    variant: 'error',
    open: false,
    message: ''
});

export default function(state = defaultState(), action: any) {
    switch (action.type) {
        case 'SHOW_MESSAGE': {
            return {
                ...state,
                ...action.payload
            }
        }
        case 'CLOSE_MESSAGE': {
            return {
                ...state,
                ...action.payload
            }
        }
        default:
            return state
    }
}