import React, { useState, useEffect } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { connect } from 'react-redux';
import AppBar from '@/components/app-bar';
import { Observable, of } from 'rxjs';
import { AjaxResponse } from 'rxjs/ajax';
import { map, catchError } from 'rxjs/operators';
import {
    Box,
    ButtonGroup,
    Button,
    CircularProgress,
    Container,
    FormControl,
    RadioGroup,
    FormControlLabel,
    Radio
} from '@material-ui/core';
import {
    fetchUsersList$,
    fetchCowlitesList$,
    fetchPensList$
} from '@/api';
import useStyles from './styles';
import { 
    saveContent,
    saveReviewDetail
} from '@/views/review-detail/redux/actions';
import url from "url";

interface FetchFunc {
    [index:string]: (params: object) => Observable<AjaxResponse>;
}
interface IndexMap {
    [index: string]: string;
}
interface IProps extends RouteComponentProps<{type: string}> {
    saveContent: (params: object) => void;
    saveReviewDetail: (params: object) => void;
    value: string;
}

const Type: any = {
    'user': '选择评估人',
    'cow': '选择牛只',
    'cowshed': '选择牛舍'
};
const FetchFunc: FetchFunc = {
    'user': fetchUsersList$,
    'cow': fetchCowlitesList$,
    'cowshed': fetchPensList$,
};
const mapStateToProps = (state: any, ownProps: any) => {
    return {
        ...state.reviewDetailStore
    }
};

const CommonSelectList: React.FC<IProps> = props => {
    const classes = useStyles();
    const [page, setPage] = useState(1);
    const [itemValue, setItemValue] = useState(props.value);
    const [next, setNext] = React.useState(null);
    const [list, setList] = React.useState([]);
    const [loading, setLoading] = React.useState(false);
    const [searchValue, setSearchValue] = useState('');
    const type: string = props.match.params.type;
    const Label = Type[type];

    const loadMore = () => {
        if (next) {
            setPage(page + 1);
        }
    };
    const handleSelectItem = (e: any) => {
        setItemValue(e.target.value);
    };
    const handleConfirm = () => {
        if(!itemValue) return;
        // const { query } = url.parse(props.location.search, true);
        const item: any = list.find((item: any) => item.id === itemValue);
        const StoreMap: IndexMap = {
            'user': 'assesser',
            'cow': 'cow',
            'cowshed': 'cowshed'
        };
        const Params = {
            [StoreMap[type]]: {
                label: item.label,
                value: itemValue
            },
            // index: query.index
        };
        // const operaFunc = type === 'user' ? props.saveReviewDetail : props.saveContent;
        // operaFunc(Params);
        props.saveReviewDetail(Params);
        props.history.goBack();
    };

    useEffect(() => {
        setLoading(true);
        const Params: any = {
            page,
            page_size: 5
        };
        if(searchValue) {
            Params.search = searchValue;
        }
        const ApiInst = FetchFunc[type](Params).pipe(
            map(({response}) => response),
            map(response => [response.results, response.next]),
            map(([results, next]) => [results.map((item: any) => ({...item, id: String(item.id)})), next]),
            map(([results, next]) =>{
                const LabelMap: IndexMap = {
                    'user': 'username',
                    'cow': 'eartag',
                    'cowshed': 'pen_name'
                };
                return [
                    results.map((item: any) => ({...item, label: item[LabelMap[type]]})),
                    next
                ]
            }),
            catchError(err => {
                return of(`err => `, err)
            })
        ).subscribe(
            (data: any) => {
                console.log('data', data);
                setList(list.concat(data[0]));
                setNext(data[1]);
                setLoading(false);
            }
        );
        return  () => ApiInst.unsubscribe();
    }, [page, searchValue]);
    return (
        <Box>
            <AppBar
                title={Label}
                // useSearch
                // searchPlaceholder="请输入要搜索的内容"
                // getSearchValue={setSearchValue}
            />
            <Container className={classes.contantContainer} >
                <FormControl component="fieldset">
                    <RadioGroup
                        aria-label="position"
                        name="position"
                        value={itemValue}
                        onChange={handleSelectItem}
                    >
                        {
                            list.map((item: any) => (
                                <FormControlLabel
                                    key={item.id}
                                    value={item.id}
                                    control={<Radio color="primary" />}
                                    label={item.label}
                                    labelPlacement="end"
                                />
                            ))
                        }
                    </RadioGroup>
                </FormControl>
            </Container>
            <Choose>
                <When condition={loading}>
                    <Box display="flex" justifyContent="center" alignItems="center" className="text-center" marginTop="20px" fontSize="12px">
                        <CircularProgress size={15} className={classes.circularProgress} />正在加载中...
                    </Box>
                </When>
                <Otherwise>
                    <Box className="text-center" marginTop="20px" onClick={() => loadMore()} fontSize="12px">
                        {next ? '点击加载更多' : '没有更多了'}
                    </Box>
                </Otherwise>
            </Choose>
            <Box position="fixed" left={15} right={15} bottom={15}>
                <ButtonGroup
                    fullWidth
                    size="medium"
                    variant="contained"
                    color="primary"
                    aria-label="选择一项"
                    onClick={() => handleConfirm()}
                >
                    <Button>
                        确定
                    </Button>
                </ButtonGroup>
            </Box>
        </Box>
    )
}
export default connect(mapStateToProps, { saveContent, saveReviewDetail })(CommonSelectList);