import React from 'react';
import { RouteComponentProps } from 'react-router-dom';
import {
    Box,
    Grid,
    Divider,
    Table,
    TableHead,
    TableBody,
    TableCell,
    TableRow
} from '@material-ui/core';
import Rating from '@material-ui/lab/Rating';
import clsx from 'clsx';
import { CircularProgressbar, buildStyles } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import AppBar from '@/components/app-bar';
import { fetchTodoTaskRunsDetail$ as fetchData$ } from '@/api';
import { map } from 'rxjs/operators';
import useStyles from './styles';

function createData(cowNumber: string, cowshed: string, date: string, statusLabel: string) {
    return { cowNumber, cowshed, date, statusLabel };
}
  
const rows = [
    createData('1001', '2号棚', '2019-03-10', '是'),
    createData('1002', '2号棚', '2019-03-10', '否'),
    createData('1003', '2号棚', '2019-03-10', '是')
];
const TasksDetail: React.FC<RouteComponentProps<{id: string}>> = props => {
    const classes = useStyles();
    const [Data, setData] = React.useState([]);

    React.useEffect(() => {
        const id = props.match.params.id;
        const ApiInst = fetchData$({id}).pipe(
            map(({response}) => response)
        ).subscribe(
            (data: any) => {
                console.log('data', data);
                setData(data);
            }
        );
        return () =>  ApiInst.unsubscribe();
    }, []);

    return (
        <Box>
            <AppBar title="完工单明细" />
            <Grid container className={classes.root} alignItems="center">
                <Grid item xs>
                <Box component="fieldset" mb={3} borderColor="transparent">
                    <Box fontSize={12}>任务 100， 完成 75</Box>
                    <Rating name="half-rating" value={75 * 5 / 100} readOnly />
                </Box>
                </Grid>
                <Grid item  xs="auto">
                    <CircularProgressbar
                        className={classes.circularProgressba}
                        value={75}
                        text='75%'
                        styles={buildStyles({
                            pathColor: `rgba(207, 0, 15, ${75 / 100})`,
                            textColor: `rgba(207, 0, 15, ${75 / 100})`
                        })}
                    />
                </Grid>
            </Grid>
            <Divider />
            <Table className={classes.table}>
                <TableHead>
                    <TableRow>
                        <TableCell align="center" className={clsx(classes.tablecell, classes.tableHeader)}>牛号</TableCell>
                        <TableCell align="center" className={clsx(classes.tablecell, classes.tableHeader)}>牛舍</TableCell>
                        <TableCell align="center" className={clsx(classes.tablecell, classes.tableHeader)}>任务日期</TableCell>
                        <TableCell align="center" className={clsx(classes.tablecell, classes.tableHeader)}>是否完成</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows.map(row => (
                        <TableRow hover key={row.cowNumber}>
                            <TableCell align="center" className={classes.tablecell} onClick={() => props.history.push('/cowDetail/1')}>{row.cowNumber}</TableCell>
                            <TableCell align="center" className={classes.tablecell}>{row.cowshed}</TableCell>
                            <TableCell align="center" className={classes.tablecell}>{row.date}</TableCell>
                            <TableCell align="center" className={classes.tablecell}>{row.statusLabel}</TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </Box>
    )
}
export default TasksDetail;