import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Link as RouterLink, Redirect } from 'react-router-dom';
import {
    Container,
    TextField,
    InputAdornment,
    Button,
    ButtonGroup,
    Grid,
    Link,
    CircularProgress
} from '@material-ui/core';
import { mergeMap, catchError } from 'rxjs/operators';
import { loginIn$ } from '@/api';
import useStyles from './styles';

const mapDispatchToProps = (dispatch: any) => {
    return {
        saveUserInfo: (userInfo: object) => dispatch({
            type: 'SAVE_USER_INFO',
            payload: {
                userInfo
            }
        })
    }
};
const mapStateToProps = (state: any) => {
    const { userInfoStore } = state;
    return {
        isLogin: userInfoStore.isLogin
    }
};

interface State {
    mobileCode: string;
    userNameOrMobile: string;
}
interface StateValid {
    mobileCode: boolean;
    userNameOrMobile: boolean;
}
const Login: React.FC = (props: any) => {
    const [loginInfo, setLoginInfo] = useState<State>({
        mobileCode: '1234',
        userNameOrMobile: 'test'
    });
    const [loginInfoValid, setLoginInfoValid] = useState<StateValid>({
        mobileCode: true,
        userNameOrMobile: true
    });
    const classes = useStyles();

    const handleUserInfoChange = (name: keyof State) => (e: React.ChangeEvent<HTMLInputElement>) => {
        setLoginInfo({...loginInfo, [name]: e.target.value})
    }
    const handleLogin = () => {
        setLoginInfoValid({
            mobileCode: Boolean(loginInfo.mobileCode),
            userNameOrMobile: Boolean(loginInfo.userNameOrMobile)
        });
        if (!loginInfo.mobileCode || !loginInfo.userNameOrMobile) {
            return
        }
        const Params = {
            'open_id': 'af7ca86e-94e6-4216-9ae4-e0ca2800125c'
        };
        const subscription = loginIn$(Params).pipe(
            mergeMap(({ response = {}}) => {
                return Promise.resolve(response)
            })
        ).subscribe(
            data => {
                props.saveUserInfo({
                    ...data,
                    isLogin: true
                });
                subscription.unsubscribe();
                props.history.push('/selectRanch');
            },
            error => {
                console.log('error', error)
            }
        )
    }
    return (
        <Container >
            {/* <If condition={props.isLogin}>
                <Redirect to="/home" />
            </If> */}
            <h1 className={`${classes.loginTitle} text-center`}>
                登录阿拉钉牧场
            </h1>
            <h4 className={`${classes.loginSubTitle}  text-center`}>
                请用您的手机号或者账号登陆
            </h4>
            <div className={classes.formContainer}>
                <TextField
                    required
                    error={!loginInfoValid.userNameOrMobile}
                    helperText={ !loginInfoValid.userNameOrMobile ? "请输入手机号/账号" : ""}
                    className={classes.textField}
                    onChange={handleUserInfoChange('userNameOrMobile')}
                    value={loginInfo.userNameOrMobile}
                    placeholder="手机号/账号"
                    fullWidth
                />
                <TextField
                    error={!loginInfoValid.mobileCode}
                    helperText={ !loginInfoValid.mobileCode ? "请输入验证码" : ""}
                    className={classes.textField}
                    onChange={handleUserInfoChange('mobileCode')}
                    value={loginInfo.mobileCode}
                    placeholder="验证码"
                    fullWidth
                    InputProps={{
                        endAdornment: (
                            <InputAdornment position="end">
                                <Button variant="contained" className={classes.codeBtn} size="small" color="primary">
                                    获取验证码
                                </Button>
                            </InputAdornment>
                        )
                    }}
                />
                <ButtonGroup
                    className={classes.textField}
                    fullWidth
                    size="medium"
                    variant="contained"
                    color="primary"
                    aria-label="Full width outlined button group"
                    onClick={() => handleLogin()}
                >
                    <Button>
                        登录
                    </Button>
                </ButtonGroup>
            </div>
            <Grid container className={classes.register}>
                <Grid item xs={2} >
                    
                </Grid>
                <Grid item xs={6} >
                    <span>我还没有账号</span>
                </Grid>
                <Grid item xs={4}>
                    <Link component={RouterLink} to="/register"> 我要注册 </Link>
                </Grid>
            </Grid>
        </Container>
    );
}
export default connect(mapStateToProps, mapDispatchToProps)(Login)