/**
 * Home Page Header Area 
 */
import React from "react";
import { Link as RouteLink } from 'react-router-dom';
import { Container, Grid, IconButton, Link, Menu, MenuItem } from '@material-ui/core';
import {
    PermIdentity,
    MoreVert as MoreVertIcon,
    PowerSettingsNew
} from '@material-ui/icons';
import useStyles from "./_styles";
import props from "ramda/es/props";

const ITEM_HEIGHT = 48;

export default (props: any) => {
    const classes = useStyles();
    const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
    const open = Boolean(anchorEl);

    const Items = [
        {
            label: '我的',
            component: <PermIdentity color="primary" />,
            cb() {
                props.history.push('/user')
            }
        },
        {
            label: '退出',
            component: <PowerSettingsNew color="primary" />,
            cb() {
                props.userLogin({
                    userInfo: {
                        isLogin: false,
                        token: '',
                        farm_code: ''
                    }
                });
                props.history.push('/login')
            }
        }
    ];
    function handleMenuItemClick(cb: () => void) {
        handleClose();
        cb();
    }
    function handleClick(event: React.MouseEvent<HTMLElement>) {
        setAnchorEl(event.currentTarget);
    }

    function handleClose() {
        setAnchorEl(null);
    }
    return (
        <Container className={classes.aldFarm}>
            <Grid container>
                <Grid item xs={8}>
                    <h4>ALDFarm</h4>
                    <h4>连接牧场一切信息</h4>
                </Grid>
                <Grid item xs={4} className="text-right">
                    <IconButton
                        aria-label="user"
                        className={classes.textWhite}
                        onClick={handleClick}
                    >
                        <MoreVertIcon />
                    </IconButton>
                </Grid>
                <Menu
                    id="long-menu"
                    anchorEl={anchorEl}
                    keepMounted
                    open={open}
                    onClose={handleClose}
                    PaperProps={{
                        style: {
                            maxHeight: ITEM_HEIGHT * 4.5,
                        },
                    }}
                >
                    {Items.map(option => (
                        <MenuItem
                            key={option.label}
                            onClick={() => handleMenuItemClick(option.cb)}
                        >
                            {option.component} {option.label}
                        </MenuItem>
                    ))}
                </Menu>
            </Grid>
        </Container>
    );
};
