interface IndexFuncMap<T> {
    [index: string]: (state: any) => T;
}
// 验证数据的必填
export const validateReviewType: IndexFuncMap<boolean> = {
    // 乳头评分
    0: (state: any) => {
        // const { assesser, content } = state;
        // return (
        //     !assesser.value ||
        //     !content.every((contentItem: any) => (
        //         contentItem.cow.value &&
        //         contentItem.lf &&
        //         contentItem.lb &&
        //         contentItem.rf &&
        //         contentItem.rb
        //     )) ||
        //     false
        // )
        const { assesser, cow, lf, lb, rf, rb, photo } = state;
        return (
            // !assesser.value ||
            !cow.value ||
            !photo.length ||
            !lf ||
            !lb ||
            !rf ||
            !rb ||
            false
        )
    },
    // 行走评分
    1: (state: any) => {
        const { assesser, photo, cow, cowType, results } = state;
        return (
            // !assesser.value ||
            !cow.value ||
            !photo.length ||
            !cowType ||
            !results ||
            false
        )
    },
    // 体况评分
    2: (state: any) => {
        const { assesser, results, cowType, cow, photo } = state;
        return (
            // !assesser.value ||
            !results ||
            !cowType ||
            !cow.value ||
            !photo.length ||
            false
        )
    },
    // 粪便评分
    3: (state: any) => {
        // const { assesser, content } = state;
        // return (
        //     !assesser.value ||
        //     !content.every((contentItem: any) => (
        //         contentItem.cowshed.value &&
        //         contentItem.cowType &&
        //         contentItem.results
        //     )) ||
        //     false
        // )
        const { assesser, cowshed, cowType, results } = state;
        return (
            // !assesser.value ||
            !cowshed.value ||
            !cowType ||
            !results ||
            false
        );
    }
};
// 格式化数据
export const formatReviewType: IndexFuncMap<object> = {
    // 乳头评分
    0: (state: any) => {
        return {
            cow_id: state.cow.value,
            note: state.note,
            lf: state.lf,
            rf: state.rf,
            rb: state.rb,
            lb: state.lb,
            assess_time: state.assessDate,
            assesser: state.assesser.value,
            add_event: state.add_event
        }
    },
    // 行走评分
    1: (state: any) => {
        return {
            cow_id: state.cow.value,
            note: state.note,
            cow_type: state.cowType,
            locomotion_score: state.results,
            assess_time: state.assessDate,
            assesser: state.assesser.value,
            add_event: state.add_event,
        }
    },
    // 体况评分
    2: (state: any) => {
        return {
            cow_id: state.cow.value,
            note: state.note,
            bcs: state.results,
            assess_time: state.assessDate,
            assesser: state.assesser.value,
            add_event: state.add_event,
            cow_type: state.cowType
        }
    },
    // 粪便评分
    3: (state: any) => {
        return {
            cow_type: state.cowType,
            fs:  state.results,
            pen: state.cowshed.value,
            note: state.note,
            assess_time: state.assessDate,
            assesser: state.assesser.value,
            add_event: state.add_event
        }
    }
};