import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Link as RouterLink } from 'react-router-dom';
import { makeStyles } from '@material-ui/styles';
import Container from '@material-ui/core/Container';
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';

const mapDispatchToProps = (dispatch: any) => {
    return {
        changeLoginStatus: (loginStatus: boolean) => dispatch({
            type: 'CHANGE_LOGIN_STATUS',
            payload: {
                loginStatus
            }
        })
    }
}
const useStyles = makeStyles({
    loginTitle: {
        fontSize: '24px',
        marginTop: '15vh'
    },
    formContainer: {
        width: '75%',
        margin: '0 auto'
    },
    codeBtn: {
        minWidth: '84px'
    },
    textField: {
        marginTop: '40px'
    },
    register: {
        marginTop: '20px',
        fontSize: '12px'
    },
    registerSuccess: {
        width: '80%',
        margin: '15vh auto 0',
        color: '#999',
        textAlign: 'center',
        fontSize: '14px'
    }
});

const Register: React.FC = (props: any) => {
    const [userNameOrMobile, setUserNameOrMobile] = useState('');
    const [mobileCode, setMobileCode] = useState('');
    const [hasRegister, setHasRegister] = useState(false);
    const classes = useStyles();

    const onUserNameOrMobileChange = (e: any) => {
        setUserNameOrMobile(e.target.value);
    }
    const onMobileCodeChange = (e: any) => {
        setMobileCode(e.target.value);
    }
    const handleLogin = () => {
        props.changeLoginStatus(true)
        setHasRegister(true)
    }
    const toHome = () => {
        props.history.push('/')
    }
    return (
        <Container >
            <h1 className={`${classes.loginTitle} text-center`}>
                阿拉钉牧业通用平台
            </h1>
            <Choose>
                <When condition={!hasRegister}>
                    <div className={classes.formContainer}>
                        <TextField
                            className={classes.textField}
                            onChange={e => onUserNameOrMobileChange(e)}
                            placeholder="录入您的手机号"
                            fullWidth
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <AccountCircle />
                                    </InputAdornment>
                                )
                            }}
                        />
                        <TextField
                            className={classes.textField}
                            onChange={e => onMobileCodeChange(e)}
                            // label="手机号/账号"
                            placeholder="验证码"
                            fullWidth
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <AccountCircle />
                                    </InputAdornment>
                                ),
                                endAdornment: (
                                    <InputAdornment position="end">
                                        <Button variant="contained" className={classes.codeBtn} size="small" color="primary">
                                            获取验证码
                                        </Button>
                                    </InputAdornment>
                                )
                            }}
                        />
                        <TextField
                            className={classes.textField}
                            onChange={e => onUserNameOrMobileChange(e)}
                            // label="手机号/账号"
                            placeholder="录入您的牧场/企业"
                            fullWidth
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position="start">
                                        <AccountCircle />
                                    </InputAdornment>
                                )
                            }}
                        />
                        <ButtonGroup
                            className={classes.textField}
                            fullWidth
                            size="medium"
                            variant="contained"
                            color="primary"
                            aria-label="Full width outlined button group"
                        >
                            <Button
                                onClick={() => handleLogin()}
                            >
                                注册
                            </Button>
                        </ButtonGroup>
                        <Grid container className={classes.register}>
                            <Grid item xs={1} >
                                
                            </Grid>
                            <Grid item xs={7} >
                                <span>已经有账户</span>
                            </Grid>
                            <Grid item xs={4} className="text-center">
                                <Link component={RouterLink} to="/login">登录</Link>
                            </Grid>
                        </Grid>
                    </div>
                </When>
                <Otherwise>
                    <div className={classes.registerSuccess}>
                        <h3>欢迎使用，有任何问题请致电：</h3>
                        <h3>18621812625</h3>
                        <ButtonGroup
                            className={classes.textField}
                            fullWidth
                            size="medium"
                            variant="contained"
                            color="primary"
                            aria-label="Full width outlined button group"
                        >
                            <Button
                                onClick={() => toHome()}
                            >
                                继续
                            </Button>
                        </ButtonGroup>
                    </div>
                </Otherwise>
            </Choose>
        </Container>
    );
}
export default connect(null, mapDispatchToProps)(Register)