import React, { useState } from 'react';
import { connect } from 'react-redux';
import {
    Container,
    FormControl,
    TextField,
    InputAdornment,
    MenuItem,
    Input,
    Box
} from '@material-ui/core';
import {
    AddAPhoto as AddAPhotoIcon,
    AddCircleOutline as AddCircleOutlineIcon
} from '@material-ui/icons';
import TextAreaAndUploadImg from '@/components/textarea-upload-img';
import {
    MuiPickersUtilsProvider,
    DatePicker,
} from '@material-ui/pickers';
import { map, concatMap } from 'rxjs/operators';
import LuxonUtils from "@date-io/luxon";
import AppBar from '@/components/app-bar';
import useStyles from './styles';
import { fetchTodoTaskTypes$, uploadImgs$, postMTodoTasks$ } from '@/api';
import { 
    saveContent,
    clearState,
    addContent,
    saveReviewDetail
} from '@/views/review-detail/redux/actions';
import { showMessage } from '@/redux/actions';
import Button from '@/components/button';
import Required from '@/components/required';

const RanchType: any = {
    ald: '阿拉钉牧场',
    society: '社会牧场'
};

const mapStateToProps = (state: any) => {
    return {
        ...state.reviewDetailStore
    }
};

const ViewCowshed: React.FC = (props: any) => {
    const { ranchType } = props.match.params;
    const classes = useStyles();
    const [postLoading, setPostLoading] = React.useState(false);
    const [TypeList, setTypeList] = useState([]);
    const [SubTypeList, setSubTypeList] = useState([]);
    const TitleText: string = `${RanchType[ranchType]}-牛舍巡棚`;

    const beforeRouteLeave = () => {
        props.clearState();
    };

    function handleUploadImgs() {
        const formdata: FormData = new FormData();
        props.photo.forEach((imgFile: any) => formdata.append('imgs', imgFile));
        const uploadImgObser =  uploadImgs$(formdata).pipe(
            map(({ response }) => response.upload_imgs)
        );
        return uploadImgObser;
    };
    const handleSubmit = () => {
        if(postLoading) return;
        const hasError = !props.type || !props.sub_type || !props.note || !props.photo.length || !props.cowshed.value;
        if (hasError) {
            console.log('hasError', '存在必填项为填写');
            props.showMessage({
                variant: 'warning',
                open: true,
                message: '存在未填充的必填项!'
            });
            return;
        }
        const retData = {
            type: props.type,
            sub_type: props.sub_type,
            location: props.cowshed.value,
            content: props.note,
            photo: props.photo.toString(),
            tasker: props.assesser.value,
            start_time: props.assessDate2,
            note: props.note
        };
        setPostLoading(true);
        const uploadImgOb$ = handleUploadImgs();
        const apiInst = uploadImgOb$.pipe(
            concatMap(upload_imgs => {
                retData.photo = upload_imgs.toString();
                return postMTodoTasks$(JSON.stringify(retData));
            })
        ).subscribe(
            data => {
                console.log('data', data);
                props.showMessage({
                    variant: 'success',
                    open: true,
                    message: '操作成功'
                });
                beforeRouteLeave();
            },
            error => {
                console.error('error', error);
                setPostLoading(false);
            },
            () => {
                setPostLoading(false);
                apiInst.unsubscribe();
            }
        );
    };
    React.useEffect(() => {
        const activeTypeItem: any = TypeList.find((item: any) => +item.id === props.type);
        const SubTypeList = activeTypeItem && activeTypeItem.sub_tasktypes;
        setSubTypeList(SubTypeList || []);
    }, [TypeList, props.type]);

    React.useEffect(() => {
        const ApiInst = fetchTodoTaskTypes$({page: 1, page_size:100}).pipe(
            map(({response}) => response)
        ).subscribe(
            data => {
                console.log('data', data);
                setTypeList(data);
            },
            err => {
                console.log('err', err)
            },
        );
        return () => ApiInst.unsubscribe();
    }, []);

    return (
        <div>
            <AppBar title={TitleText} beforeRouteLeave={beforeRouteLeave} />
            <Container className={classes.root}>
                <FormControl margin="dense" fullWidth>
                    <Input
                        onFocus={() => props.history.push('/commonSelectList/user')}
                        value={props.assesser.label}
                        placeholder="点击选择责任人"
                        startAdornment={
                            <InputAdornment className={classes.inputAdornment} position="start">
                                责任人:
                            </InputAdornment>
                        }
                    />
                </FormControl>
                <FormControl margin="dense" fullWidth>
                    <TextField
                        select
                        InputProps={{
                            startAdornment: (
                                <InputAdornment className={classes.inputAdornment} position="start">
                                    <p>
                                        <Required />任务类型:
                                    </p>
                                </InputAdornment>
                            ),
                        }}
                        onChange={e => props.saveReviewDetail({
                            type: e.target.value
                        })}
                        value={props.type}
                    >
                        {TypeList.map((option: any) => (
                            <MenuItem key={option.id} value={option.id}>
                                {option.task_name_lang}
                            </MenuItem>
                        ))}
                    </TextField>
                </FormControl>
                <FormControl margin="dense" fullWidth>
                    <TextField
                        select
                        InputProps={{
                            startAdornment: (
                                <InputAdornment className={classes.inputAdornment2} position="start">
                                    <p>
                                        <Required />子任务类型:
                                    </p>
                                </InputAdornment>
                            ),
                        }}
                        onChange={e => props.saveReviewDetail({
                            sub_type: e.target.value
                        })}
                        value={props.sub_type}
                    >
                        {SubTypeList.map((option: any) => (
                            <MenuItem key={option.id} value={option.id}>
                                {option.task_name_lang}
                            </MenuItem>
                        ))}
                    </TextField>
                </FormControl>
                <FormControl fullWidth margin="normal">
                    <MuiPickersUtilsProvider utils={LuxonUtils} locale="zh-cn">
                        <DatePicker
                            id="mui-pickers-date"
                            label="巡棚日期"
                            format="yyyy-MM-dd"
                            value={props.assessDate2}
                            onChange={date => props.saveReviewDetail({assessDate2: date})}
                            okLabel="确定"
                            cancelLabel="取消"
                            shouldDisableDate={ day => {
                                if (day === null) {
                                    return true;
                                }
                                return day.toMillis() < props.assessDate.toMillis();
                            }}
                        />
                    </MuiPickersUtilsProvider>
                </FormControl>
                <Box>
                    <FormControl margin="dense" fullWidth>
                        <Input
                            value={props.cowshed.label}
                            onFocus={() => props.history.push(`/commonSelectList/cowshed`)}
                            placeholder="点击选择牛舍号"
                            startAdornment={
                                <InputAdornment className={classes.inputAdornment} position="start">
                                    <p>
                                        <Required />巡视点:
                                    </p>
                                </InputAdornment>
                            }
                            endAdornment={<InputAdornment position="end" className={`${classes.inputAdornment} text-right`}>#犊牛舎</InputAdornment>}
                        />
                    </FormControl>
                    <TextAreaAndUploadImg
                        labelText='巡视点，问题描述'
                        photo={props.photo}
                        note={props.note}
                        saveReviewDetail={props.saveReviewDetail}
                        textAreaRequired={true}
                    />
                </Box>
                {/* {
                    props.content.map((contentItem: any, index: number) => (
                        <Box key={`content-item-${index}`}>
                            <FormControl margin="dense" fullWidth>
                                <Input
                                    value={contentItem.cowshed.label}
                                    onFocus={() => props.history.push(`/commonSelectList/cowshed?index=${index}`)}
                                    placeholder="点击选择牛舍号"
                                    startAdornment={<InputAdornment className={classes.inputAdornment} position="start">巡视点:</InputAdornment>}
                                    endAdornment={<InputAdornment position="end" className={`${classes.inputAdornment} text-right`}>#犊牛舎</InputAdornment>}
                                />
                            </FormControl>
                            <TextAreaAndUploadImg
                                labelText='巡视点，问题描述'
                                index={index}
                                photo={contentItem.photo}
                                note={contentItem.note}
                                saveContent={props.saveContent}
                            />
                        </Box>
                    ))
                } */}
                {/* <Box className={classes.flexbox}>
                    <IconButton onClick={() => props.addContent()}>
                        <AddCircleOutlineIcon color="primary" />
                    </IconButton>   
                </Box> */}
                <Box className={classes.btnContainer}>
                    <Button
                        btnText="提交"
                        onClick={() => handleSubmit()}
                        className={classes.btnMargin}
                        loading={postLoading}
                    />
                </Box>
            </Container>
        </div>
    )
};
const actions = {
    saveContent,
    clearState,
    addContent,
    saveReviewDetail,
    showMessage
};
export default connect(mapStateToProps,actions)(ViewCowshed);