import React from 'react';
import { RouteComponentProps } from 'react-router-dom';
import {
    Box,
    Container,
    ExpansionPanel,
    ExpansionPanelDetails,
    ExpansionPanelSummary,
    Typography,
    Grid,
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableRow
} from '@material-ui/core';
import clsx from 'clsx';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import AppBar from '@/components/app-bar';
import Divider from '@/components/divider';
import useStyles from './styles';
import { fetchCalfsDetail$ as fetchData$ } from '@/api';
import { map } from 'rxjs/operators';

const createData = (label: string, value: string | number) => ({ label, value });
const CowInfo = [
    createData('牛号', 3080),
    createData('牛区', 11),
    createData('离场日期', ''),
    createData('生日', '2013-06-07'),
    createData('胎次', 5),
    createData('胎次组', 3),
    createData('母牛号', 30),
    createData('父牛号', 380)
];
function createEventData(eventname: string, date: string, days: number, count: number, freeze: string, mark: string) {
    return { eventname, date, days, count, freeze, mark };
  }
  
const eventData = [
    createEventData('转群1转群1转群1转群1', '2019-06-17', 1000, 24, 'DNJSKWKWN', ''),
    createEventData('转群2', '2019-06-17', 1000, 24, 'DNJSKWKWN', ''),
    createEventData('转群3', '2019-06-17', 1000, 24, 'DNJSKWKWN', ''),
    createEventData('转群4', '2019-06-17', 1000, 24, 'DNJSKWKWN', ''),
    createEventData('转群5', '2019-06-17', 1000, 24, 'DNJSKWKWN', ''),
    createEventData('转群6', '2019-06-17', 1000, 24, 'DNJSKWKWN', ''),
    createEventData('转群7', '2019-06-17', 1000, 24, 'DNJSKWKWN', ''),
    createEventData('转群8', '2019-06-17', 1000, 24, 'DNJSKWKWN', ''),
    createEventData('转群9', '2019-06-17', 1000, 24, 'DNJSKWKWN', ''),
    createEventData('转群10', '2019-06-17', 1000, 24, 'DNJSKWKWN', ''),
];
const CowDetail: React.FC<RouteComponentProps<{id: string}>> = props => {
    const classes = useStyles();
    const TitleText = `牛头详情-${props.match.params.id}号`;
    const [Data, setData] = React.useState([]);

    const [expanded, setExpanded] = React.useState<string | false>('panel1');
    const handleChange = (panel: string) => (event: React.ChangeEvent<{}>, isExpanded: boolean) => {
        setExpanded(isExpanded ? panel : false);
    };

    React.useEffect(() => {
        const id = props.match.params.id;
        const ApiInst = fetchData$({id}).pipe(
            map(({response}) => response)
        ).subscribe(
            (data: any) => {
                console.log('data', data);
                setData(data);
            }
        );
        return () =>  ApiInst.unsubscribe();
    }, []);

    return (
        <Box className={classes.root}>
            <AppBar title={TitleText} />
            <Container>
                <ExpansionPanel expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
                    <ExpansionPanelSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1bh-content"
                        id="panel1bh-header"    
                    >
                        <Typography className={classes.heading}>牛只信息一</Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails>
                        <Box width="100%">
                            {
                                CowInfo.map((item, i) => (
                                    <React.Fragment key={item.label}>
                                        <Grid container className={classes.infoItem}>
                                            <Grid item xs className="text-left">
                                                {item.label}
                                            </Grid>
                                            <Grid item xs className="text-right">
                                                {item.value}
                                            </Grid>
                                        </Grid>
                                        {(i !== CowInfo.length-1) && <Divider />}
                                    </React.Fragment>
                                ))
                            }
                        </Box>
                    </ExpansionPanelDetails>
                </ExpansionPanel>
                <ExpansionPanel expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
                    <ExpansionPanelSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1bh-content"
                        id="panel1bh-header"    
                    >
                        <Typography className={classes.heading}>牛只信息二</Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails>
                        <Box width="100%">
                            {
                                CowInfo.map((item, i) => (
                                    <React.Fragment key={item.label}>
                                        <Grid container className={classes.infoItem}>
                                            <Grid item xs className="text-left">
                                                {item.label}
                                            </Grid>
                                            <Grid item xs className="text-right">
                                                {item.value}
                                            </Grid>
                                        </Grid>
                                        {(i !== CowInfo.length-1) && <Divider />}
                                    </React.Fragment>
                                ))
                            }
                        </Box>
                    </ExpansionPanelDetails>
                </ExpansionPanel>
                <ExpansionPanel expanded={expanded === 'panel3'} onChange={handleChange('panel3')}>
                    <ExpansionPanelSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1bh-content"
                        id="panel1bh-header"    
                    >
                        <Typography className={classes.heading}>牛只信息三</Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails>
                        <Box width="100%">
                            {
                                CowInfo.map((item, i) => (
                                    <React.Fragment key={item.label}>
                                        <Grid container className={classes.infoItem}>
                                            <Grid item xs className="text-left">
                                                {item.label}
                                            </Grid>
                                            <Grid item xs className="text-right">
                                                {item.value}
                                            </Grid>
                                        </Grid>
                                        {(i !== CowInfo.length-1) && <Divider />}
                                    </React.Fragment>
                                ))
                            }
                        </Box>
                    </ExpansionPanelDetails>
                </ExpansionPanel>
                <ExpansionPanel expanded={expanded === 'panel4'} onChange={handleChange('panel4')}  className={expanded === 'panel4' ? classes.lastExpan : ''}>
                    <ExpansionPanelSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1bh-content"
                        id="panel1bh-header"    
                    >
                        <Typography className={classes.heading}>事件信息</Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails className={expanded === 'panel4' ? classes.lastDetail : ''}>
                        <Table>
                            <TableHead>
                                <TableRow>
                                    <TableCell className={clsx(classes.tableCell, classes.tableHeader)}>事件</TableCell>
                                    <TableCell className={clsx(classes.tableCell, classes.tableHeader)}>日期</TableCell>
                                    <TableCell className={clsx(classes.tableCell, classes.tableHeader)}>天数</TableCell>
                                    <TableCell className={clsx(classes.tableCell, classes.tableHeader)}>本胎发生的次数</TableCell>
                                    <TableCell className={clsx(classes.tableCell, classes.tableHeader)}>冻精</TableCell>
                                    <TableCell className={clsx(classes.tableCell, classes.tableHeader)}>备注</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                            {eventData.map(row => (
                                <TableRow key={row.eventname}>
                                    <TableCell className={classes.tableCell}>
                                        <div style={{width: '80px'}}>
                                            {row.eventname}
                                        </div>
                                    </TableCell>
                                    <TableCell className={classes.tableCell}>{row.date}</TableCell>
                                    <TableCell className={classes.tableCell}>{row.days}</TableCell>
                                    <TableCell className={classes.tableCell}>
                                        <div style={{width: '50px'}}>
                                            {row.count}
                                        </div>
                                    </TableCell>
                                    <TableCell className={classes.tableCell}>{row.freeze}</TableCell>
                                    <TableCell className={classes.tableCell}>
                                        <div style={{width: '80px'}}>
                                            {row.mark}
                                        </div>
                                    </TableCell>
                                </TableRow>
                            ))}
                            </TableBody>
                        </Table>
                    </ExpansionPanelDetails>
                </ExpansionPanel>
            </Container>
        </Box>
    )
}
export default CowDetail;