    const { ranchType, reviewType }: { ranchType: string, reviewType: keyof ReviewType  } = props.match.params;
    const isCowNo = +reviewType !== 3; // 是否是牛号
    const isTK = +reviewType === 2; // 是否是体况评分
    const LabelText: string = `${ReviewType[reviewType]}-业务备注`;
    const ResultsList = isTK ? BCS : Results;
    

    const beforeRouteLeave = () => {
        props.clearState();
    };
    function handleUploadImgs() {
        // const photoArr = props.content.map((contentItem: any) => contentItem.photo);
        // const rxOb: FormData[] = photoArr.map((item: any) => {
        //     const formdata: FormData = new FormData();
        //     item.forEach((imgFile: any) => formdata.append('imgs', imgFile));
        //     return formdata;
        // });
        // const uploadImgObser =  from(rxOb).pipe(
        //     concatMap(formdata => {
        //         return formdata.has('imgs') ? uploadImgs$(formdata) : Promise.resolve({ response: { upload_imgs: []} });
        //     }),
        //     map(({ response }) => response)
        // );
        const formdata: FormData = new FormData();
        props.photo.forEach((imgFile: any) => formdata.append('imgs', imgFile));
        const uploadImgObser =  uploadImgs$(formdata).pipe(
            map(({ response }) => response.upload_imgs)
        );
        return uploadImgObser;
    };
    const handleSubmit = () => {
        if(postLoading) return;
        const OperaFuncMap: ReviewDetail.FetchFunc = {
            0: postAssessTeats$,
            1: postAssessLocomotions$,
            2: postAssessBcss$,
            3: postAssessFaecess$
        };
        const hasError = validateReviewType[reviewType](props);
        if (hasError) {
            console.log('hasError', '存在必填项为填写');
            props.showMessage({
                variant: 'warning',
                open: true,
                message: '存在未填充的必填项!'
            });
            return;
        }
        setPostLoading(true);
        const retData: any = formatReviewType[reviewType](props);
        const uploadImgOb$ = handleUploadImgs();
        const postAssessTeatsInst = uploadImgOb$.pipe(
            concatMap(upload_imgs => {
                retData.photo = upload_imgs.toString();
                const OperaFunc = OperaFuncMap[reviewType];
                return OperaFunc(JSON.stringify(retData));
            })
        ).subscribe(
            data => {
                console.log('data', data);
                props.showMessage({
                    variant: 'success',
                    open: true,
                    message: '操作成功'
                });
                beforeRouteLeave();
            },
            error => {
                console.error('error', error);
                setPostLoading(false);
            },
            () => {
                setPostLoading(false);
                postAssessTeatsInst.unsubscribe();
            }
        );
    };
   

