import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
    Paper,
    Tabs,
    Tab,
    Grid,
    Card,
    CardActionArea,
    CardActions,
    CardMedia,
    Button
} from '@material-ui/core';
import AppBar from '@/components/app-bar';

const useStyles = makeStyles({
    root: {
        flexGrow: 1,
        position: 'fixed',
        top: 48,
        width: '100vw'
    },
    reviewListContainer: {
        paddingTop: 96
    },
    media: {
        height: 120,
    },
});

const RouteParamsType: any = {
    'ald': '阿拉钉牧场',
    'society': '社会牧场'
};

export default (props: any) => {
    const classes = useStyles();
    const [tabValue, setTabValue] = useState(0);
    const reviewList = [
        {
            title: '乳头评分',
            img: require("./image/default.jpg"),
            value: 0
        },
        {
            title: '行走评分',
            img: require("./image/default.jpg"),
            value: 1
        },
        {
            title: '体况评分',
            img: require("./image/default.jpg"),
            value: 2
        },
        {
            title: '粪便评分',
            img: require("./image/default.jpg"),
            value: 3
        }
    ];
    const RanchType = props.match.params.type;

    function handleChange(event: React.ChangeEvent<{}>, newValue: number) {
        setTabValue(newValue);
        if (newValue) {
            props.history.push(`/viewCowshed/${RanchType}`)
        }
    }

    return (
        <div >
            <AppBar title={`${RouteParamsType[RanchType]}-现场管理清单`} />
            <Paper className={classes.root} square={true}>
                <Tabs
                    value={tabValue}
                    onChange={handleChange}
                    indicatorColor="primary"
                    textColor="primary"
                    centered
                    variant="fullWidth"
                >
                    <Tab label="现场评分" />
                    <Tab label="牛舍巡棚" />
                </Tabs>
            </Paper>
            <Grid container justify="space-around" className={classes.reviewListContainer}>
                {
                    reviewList.map(item => (
                        <Grid item xs={5} key={item.title} className="mt-15-it">
                            <Card onClick={() => props.history.push(`/reviewDetail/${RanchType}/${item.value}`) }>
                                <CardActionArea>
                                    <CardMedia
                                        className={classes.media}
                                        image={item.img}
                                        title="Contemplative Reptile"
                                    />
                                </CardActionArea>
                                <CardActions>
                                    <Button size="small" color="primary">
                                        {item.title}
                                    </Button>
                                </CardActions>
                            </Card>
                        </Grid>
                    ))
                }
            </Grid>
        </div>
    )
}
