import base from './base';

export const loginIn$ = (params: object) => base.post('jauth', params, { useToken: false });

export const fetchCanUseFarms$ = (params: object) => base.get('farms', params, { useToken: true });

export const uploadImgs$ = (params: object) => base.post('imagesupload', params, { useFarmCode: true, useToken: true });

// 任务运行列表
export const fetchTodoTaskRuns$ = (params: object) => base.get('todotaskruns', params, { useFarmCode: true, useToken: true });

// 任务运行列表单项明细(完工单明细)
export const fetchTodoTaskRunsDetail$ = (params: object) => base.get('todotaskruns', params, { useFarmCode: true, useToken: true, useId: true });

// 用户列表
export const fetchUsersList$ = (params: object) => base.get('users', params, { useFarmCode: true, useToken: true });

// 评估模块-牛号列表
export const fetchCowlitesList$ = (params: object) => base.get('cowlites', params, { useFarmCode: true, useToken: true });

// 评估模块-牛舎列表
export const fetchPensList$ = (params: object) => base.get('pens', params, { useFarmCode: true, useToken: true });

// 犊牛信息列表
export const fetchCalfsList$ = (params: object) => base.get('cows', params, { useFarmCode: true, useToken: true });

// 犊牛信息详情
export const fetchCalfsDetail$ = (params: any) => {
    const options = {
        useFarmCode: true,
        useToken: true,
        useId: true
    }

    return base.get('cows', params, options);
};

// 牛舍巡棚页面
// 任务类型
export const fetchTodoTaskTypes$ = (params: object = {}) => base.get('todotasktypes', params, { useFarmCode: false, useToken: true });

// 乳头评分
export const postAssessTeats$ = (params: object | string) => base.post('assessteats', params, { useToken: true, useFarmCode: true, useJSON: true });
// 行走评分
export const postAssessLocomotions$ = (params: object | string) => base.post('assesslocomotions', params, { useToken: true, useFarmCode: true, useJSON: true });
// 体况评分
export const postAssessBcss$ = (params: object | string) => base.post('assessbcss', params, { useToken: true, useFarmCode: true, useJSON: true });
// 粪便评分
export const postAssessFaecess$ = (params: object | string) => base.post('assessfaecess', params, { useToken: true, useFarmCode: true, useJSON: true });
// 牛舍巡棚
export const postMTodoTasks$ = (params: object | string) => base.post('mtodotasks', params, { useToken: true, useFarmCode: true, useJSON: true });

// 现场报告-清单
export const getOnsiteAssessments$ = (params: object | string) => base.get('onsiteassessments', params, { useToken: true, useFarmCode: true });
