import React, { useState } from 'react';
import {
    Container,
    IconButton,
    Box,
    Paper,
    Grid,
    FormControl,
    InputLabel,
    InputAdornment,
    Input
} from '@material-ui/core';
import {
    Settings as SettingsIcon,
    Done as DoneIcon,
    PermIdentity,
    Assignment,
    NearMe,
    Smartphone
} from '@material-ui/icons';
import {
    makeStyles
} from '@material-ui/styles';
import AppBar from '@/components/app-bar';

const useStyles = makeStyles({
    root: {
        paddingTop: 60
    },
    label: {
        fontSize: 12,
        color: '#111'
    },
    flexbox: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
    },
    paper: {
        marginTop: 20
    },
    infoContent: {
        fontSize: 14,
        padding: '10px 10px 10px 0'
    },
    input: {
        padding: '10px 0'
    },
    formControl: {
        marginTop: '20px!important'
    }
});

const editInfoArr: editInfoArrItem[] = [
    {
        label: '个性介绍',
        icon: <PermIdentity color="primary" />,
        key: 'userDesc'
    },
    {
        label: '公司名称',
        icon: <Assignment color="primary" />,
        key: 'companyName'
    },
    {
        label: '公司地址',
        icon: <NearMe color="primary" />,
        key: 'companyAddress'
    },
    {
        label: '联系方式',
        icon: <Smartphone color="primary" />,
        key: 'mobileNumber'
    }
];
interface State {
    userDesc: string;
    companyName: string;
    companyAddress: string;
    mobileNumber: string;
}
interface editInfoArrItem {
    label: string;
    icon: JSX.Element;
    key: keyof State;
}
const User: React.FC = props => {
    const [editState, setEditState] = useState(false);
    const classes = useStyles();
    const [editInfo, setEditInfo] = useState<State>({
        userDesc: '清风雨露一相逢便胜却人间无数',
        companyName: '阿拉钉科技有限公司',
        companyAddress: '北京市海淀区某某路',
        mobileNumber: '18888888888'
    });
    const handleChange = (name: keyof State) => (event: React.ChangeEvent<HTMLInputElement>) => {
        setEditInfo({...editInfo, [name]: event.target.value})
    }
    return (
        <Container className={classes.root}>
            <AppBar
                title="个人信息"
                endAdorment={
                    (<IconButton onClick={() => setEditState(!editState)} className="text-white">
                        <Choose>
                            <When condition={editState}>
                                <DoneIcon />
                            </When>
                            <Otherwise>
                                <SettingsIcon />
                            </Otherwise>
                        </Choose>
                    </IconButton>)
                }
            />
            <Box>
                {
                    editInfoArr.map((item: editInfoArrItem) => (
                        <Choose>
                            <When condition={!editState}>
                                <Paper className={classes.paper} key={item.key}>
                                    <Grid container spacing={1}>
                                        <Grid item xs={2} className={classes.flexbox}>
                                            <IconButton>
                                                {item.icon}
                                            </IconButton>
                                        </Grid>
                                        <Grid item xs>
                                            <Box className={classes.label}>
                                                {item.label}
                                            </Box>
                                            <Box className={classes.infoContent}>
                                                {editInfo[item.key]}
                                            </Box>
                                        </Grid>
                                    </Grid>
                                </Paper>
                            </When>
                            <Otherwise>
                                <FormControl fullWidth margin="dense" key={item.key} className={classes.formControl}>
                                    <InputLabel
                                        htmlFor={item.key}
                                    >
                                        {item.label}
                                    </InputLabel>
                                    <Input
                                        id="input-with-icon-adornment"
                                        startAdornment={
                                            <InputAdornment position="start">
                                                {item.icon}
                                            </InputAdornment>
                                        }
                                        multiline
                                        className={classes.formControl}
                                        value={editInfo[item.key]}
                                        onChange={handleChange(item.key)}
                                    />
                                </FormControl>
                            </Otherwise>
                        </Choose>
                    ))
                }
            </Box>
        </Container>
    )
};
export default User;