
import { SAVE_USER_INFO } from '../actionTypes';

const userInfo = JSON.parse(localStorage.getItem('userInfo') || '{}');

console.log('userInfo', userInfo)

const initState = {
    isLogin: false,
    token: '',
    farm_code: ''
};
if (userInfo.isLogin) {
    initState.isLogin = userInfo.isLogin;
    initState.token = userInfo.token;
    initState.farm_code = userInfo.farm_code;
    console.log('initState', initState);
}
export default function changeLoginStatus(state = initState, action: any) {
    switch (action.type) {
        case SAVE_USER_INFO: {
            const userInfo = JSON.parse((localStorage.getItem('userInfo') || '{}'));
            localStorage.setItem('userInfo', JSON.stringify({...userInfo, ...action.payload.userInfo}));
            return {
                ...state,
                ...action.payload.userInfo
            }
        }
        default:
            return state
    }
}