import React from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { CircularProgressbar, buildStyles } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import {
    Box,
    Grid,
    IconButton,
    Menu,
    MenuItem,
    Paper,
    Container,
    Typography,
    CircularProgress
} from '@material-ui/core';
import {
    ExpandMore
} from '@material-ui/icons';
import { map } from 'rxjs/operators';
import dayjs from 'dayjs';
// import { ListView } from 'antd-mobile';
import AppBar from '@/components/app-bar';
import Divider from '@/components/divider';
import useStyles from './styles';
import { fetchTodoTaskRuns$ } from '@/api';

interface selectedIndexState {
    BJ: number;
    FZ: number;
    SY: number;
    RW: number;
}
interface optionsItem {
    key: keyof selectedIndexState;
    anchorEl: null | HTMLElement;
    setAnchorEl: React.Dispatch<React.SetStateAction<HTMLElement | null>>;
    options: string[];
}
const ITEM_HEIGHT = 48;
// const dataBlobs = {};
// let sectionIDs = [];
// let rowIDs = [];

const Tasks: React.FC<RouteComponentProps> = props => {
    const classes = useStyles();
    const [tasksList, setTasksList] = React.useState([]);
    const [page, setPage] = React.useState(1);
    const [next, setNext] = React.useState(null);
    const [loading, setLoading] = React.useState(false);
    // const [isLoading, setIsLoading] = React.useState(true);
    const [anchorElBJ, setAnchorElBJ] = React.useState<null | HTMLElement>(null);
    const [anchorElFZ, setAnchorElFZ] = React.useState<null | HTMLElement>(null);
    const [anchorElSY, setAnchorElSY] = React.useState<null | HTMLElement>(null);
    const [anchorElRW, setAnchorElRW] = React.useState<null | HTMLElement>(null);
    const [selectedIndex, setSelectedIndex] = React.useState<selectedIndexState>({
        'BJ': 0,
        'FZ': 0,
        'SY': 0,
        'RW': 0
    });
    const options: optionsItem[] = [
        {
            key: 'BJ',
            anchorEl: anchorElBJ,
            setAnchorEl: setAnchorElBJ,
            options: [
                '保健',
                '保健一',
                '保健二',
                '保健三'
            ]
        },
        {
            key: 'FZ',
            anchorEl: anchorElFZ,
            setAnchorEl: setAnchorElFZ,
            options: [
                '繁殖',
                '繁殖一',
                '繁殖二',
                '繁殖三'
            ]
        },
        {
            key: 'SY',
            anchorEl: anchorElSY,
            setAnchorEl: setAnchorElSY,
            options: [
                '饲养',
                '饲养一',
                '饲养二',
                '饲养三'
            ]
        },
        {
            key: 'RW',
            anchorEl: anchorElRW,
            setAnchorEl: setAnchorElRW,
            options: [
                '任务',
                '任务一',
                '任务二',
                '任务三'
            ]
        }
    ];
    const setAnchorElMap: any = {
        'BJ': setAnchorElBJ,
        'FZ': setAnchorElFZ,
        'SY': setAnchorElSY,
        'RW': setAnchorElRW
    };
    const handleMenuItemClick = (event: React.MouseEvent<HTMLElement>, key: string, index: number) => {
        setSelectedIndex({...selectedIndex, [key]: index});
        handleClose(key);
    }
    
    const handleClose = (key: string) => {
        setAnchorElMap[key](null);
    }
    const loadMore = () => {
        if (next) {
            setPage(page + 1);
        }
    };

    React.useEffect(() => {
        setLoading(true);
        const ApiInst = fetchTodoTaskRuns$({page, page_size: 10}).pipe(
            map(({response}) => response),
            map(response => [response.results, response.next]),
            map(([list, next]) => {
                const tasksList =  list.map((item: any) => {
                    return {
                        ...item,
                        toNowDate: dayjs(item.run_date).diff(dayjs(), 'day')
                    }
                });
                return [tasksList, next];
            })
        ).subscribe(
            (data: any) => {
                console.log('data', data);
                setTasksList(tasksList.concat(data[0]));
                setNext(data[1]);
                setLoading(false);
            }
        );
        return () =>  ApiInst.unsubscribe();
    }, [page]);

    return (
        <Box>
            <AppBar title="任务清单" />
            <Box className={classes.selectContainer}>
                <Grid container >
                    {
                        options.map((item, index) => (
                            <Grid item xs={3} key={`${item.key}-${index}`} container justify="center">
                                <IconButton
                                    color="inherit"
                                    aria-label="More"
                                    aria-controls="long-menu"
                                    aria-haspopup="true"
                                    onClick={e => item.setAnchorEl(e.currentTarget)}
                                >
                                    <Box fontSize="12px">
                                        {item.options[selectedIndex[item.key]]}
                                    </Box>
                                    <ExpandMore />
                                </IconButton>
                                <Menu
                                    id="long-menu"
                                    anchorEl={item.anchorEl}
                                    keepMounted
                                    open={Boolean(item.anchorEl)}
                                    onClose={() => handleClose(item.key)}
                                    PaperProps={{
                                        style: {
                                            maxHeight: ITEM_HEIGHT * 4.5
                                        },
                                    }}
                                >
                                    {item.options.map((option, index) => (
                                        <MenuItem
                                            key={option}
                                            selected={index === selectedIndex[item.key]}
                                            onClick={e => handleMenuItemClick(e, item.key, index)}
                                        >
                                            {option}
                                        </MenuItem>
                                    ))}
                                </Menu>
        
                            </Grid>
                        ))
                    }
                </Grid>
            </Box>
            <Container className={classes.contantContainer}>
                {
                    tasksList.map((item: any, i) => (
                        <Paper className={classes.paper} key={`tasks-item-${i}`}>
                            <Box position="relative">
                                <Grid container>
                                    <Grid item xs={2} container justify="center" alignItems="center">
                                        <Box paddingX="5px">
                                            <img
                                                className={classes.img}
                                                alt="complex"
                                                src={item.photo ? item.photo : require('../../assets/images/default.jpg')}
                                            />
                                        </Box>
                                    </Grid>
                                    <Grid item xs={10} container>
                                        <Grid item xs="auto" container direction="column">
                                            <Grid item xs>
                                                <Typography gutterBottom variant="subtitle1">
                                                    {item.todotask_name}
                                                </Typography>
                                                <Typography variant="subtitle2" gutterBottom>
                                                    <Box fontSize="12px">
                                                        {item.content}
                                                    </Box>
                                                    <Box fontSize="12px">
                                                        100/84 完工期限 {item.toNowDate > 0 ?  item.toNowDate : 0}天
                                                    </Box>
                                                    <Box fontSize="12px">
                                                        任务日期 {item.run_date.split(' ')[0]} { item.toNowDate > 0 ? '距今' : '已超'}{Math.abs(item.toNowDate)}天
                                                    </Box>
                                                </Typography>
                                            </Grid>
                                            <Grid item>
                                                <Divider />
                                                <Typography variant="subtitle2" gutterBottom>
                                                    <IconButton className={classes.detailBtn}>
                                                        <Box fontSize="12px">
                                                            明细
                                                        </Box>
                                                        <ExpandMore color="primary" />
                                                    </IconButton>
                                                </Typography>
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                </Grid>
                                <Box
                                    position="absolute"
                                    right="5px"
                                    top="10%"
                                    onClick={() => props.history.push(`/tasksDetail/${item.id}`)}
                                >
                                    <CircularProgressbar
                                        className={classes.circularProgressba}
                                        value={75}
                                        text='75%'
                                        styles={buildStyles({
                                            pathColor: `rgba(207, 0, 15, ${75 / 100})`,
                                            textColor: `rgba(207, 0, 15, ${75 / 100})`
                                        })}
                                    />
                                </Box>
                            </Box>
                        </Paper>
                    ))
                }
                <Choose>
                    <When condition={loading}>
                        <Box display="flex" justifyContent="center" alignItems="center" className="text-center" marginTop="20px" fontSize="12px">
                            <CircularProgress size={15} className={classes.circularProgress} />正在加载中...
                        </Box>
                    </When>
                    <Otherwise>
                        <Box className="text-center" marginTop="20px" onClick={() => loadMore()} fontSize="12px">
                            {next ? '点击加载更多' : '没有更多了'}
                        </Box>
                    </Otherwise>
                </Choose>
            </Container>
        </Box>
    );
}
export default Tasks;