import React from 'react';
// import qs from "querystring";
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import url from "url";
import { makeStyles } from '@material-ui/styles';
import HomeHeader from "./header";
import Entrance from "./entrance";
import Reports from "./report";
import "./index.scss";
import { userLogin } from '@/redux/actions';

const useStyles = makeStyles({
    homeContainer: {
        // height: '100vh',
        // overflowY: 'auto',
        background: '#fff'
    }
});

interface IProps{ 
    isLogin: boolean;
}
const mapStateToProps = (state: any) => {
    const { userInfoStore } = state;
    return {
        isLogin: userInfoStore.isLogin
    }
};
const Home: React.FC<IProps> = (props: any) => {
    const classes = useStyles();
    const { query } = url.parse(props.location.search, true);
    const isALD= String(query.isALD || sessionStorage.getItem("isALD") || "0");

    return (
        <div className={classes.homeContainer}>
            <If condition={!props.isLogin}>
                <Redirect to="/login" />
            </If>
            <HomeHeader {...props} />
            <Entrance isALD={isALD} {...props} />
            <Reports />
        </div>
    )
};
export default connect(mapStateToProps, { userLogin })(Home)