import React from 'react';
import { HashRouter, Route } from 'react-router-dom';
import './App.css';
import withLayout from "./components/layout";
import Login from './views/login';
import Register from './views/register';
import SelectRanch from './views/select-ranch';
import Home from './views/home';
import Tasks from './views/tasks';
import TasksDetail from './views/tasks-detail';
import Reports from './views/reports';
import ManageList from './views/manage-listing';
import ReviewDetail from './views/review-detail';
import ViewCowshed from './views/view-cowshed';
import SceneReport from './views/scene-report';
import SearchCow from './views/search-cow';
import CowDetail from './views/cow-detail';
import SceneReportDetail from './views/scene-report-detail';
import CommonSelectList from '@/views/common-select-list';
import User from './views/user';
import ScrollToTop from './components/scroll-to-top';

export default () => {
    return (
        <HashRouter>
            <ScrollToTop>
                {/* 登录注册 */}
                <Route path="/login" component={Login} />   
                <Route path="/register" component={Register} />

                {/* 选择牧场 */}
                <Route path="/selectRanch" component={SelectRanch} />  

                {/* 首页以及首页的几个入口 */}
                <Route path={["/", "/home", "/index"]} exact component={withLayout(Home)} />
                <Route path="/manageList/:type" component={ManageList} />
                <Route path="/sceneReport" component={SceneReport} /> 
                <Route path="/SceneReportDetail/:id" component={SceneReportDetail} /> 

                {/* 底部导航栏入口 */}
                <Route path="/tasks" component={withLayout(Tasks)} />   
                <Route path="/reports" component={withLayout(Reports)} />
                <Route path="/search" component={withLayout(SearchCow)} />
                <Route path="/cowDetail/:id" component={withLayout(CowDetail)} />

                <Route path="/reviewDetail/:ranchType/:reviewType" component={ReviewDetail} />
                <Route path="/viewCowshed/:ranchType" component={ViewCowshed} />
                <Route path="/tasksDetail/:id" component={TasksDetail} />
                {/* 用户信息入口 */}
                <Route path="/user" component={User} /> 
                {/* 选择用户，选择牛只，选择牛舍 */}
                <Route path="/commonSelectList/:type" component={CommonSelectList} />
            </ScrollToTop>
        </HashRouter>
    );
};
